function showCookie() {
  let cookieWindow = document.createElement("div");
  if (!sessionStorage.cookie) {
    cookieWindow.classList.add('cookie', 'flex', '--just-space', '--align-center');
    document.body.appendChild(cookieWindow);
    cookieWindow.innerHTML = `
  <img class="cookie__icon" src="img/cookie.svg" alt="cookie icon">
    <p class="cookie__text">We use cookies to enhance your experience.By continuing to visit this site you agree to our use of cookies. <a class="cookie__link" href="#">More info</a></p>
    <button type="button" class="cookie__btn btn --secondary">Accept</button>
  `
    document.querySelector('.cookie__btn').addEventListener('click', hideCookie)
  }

  function hideCookie() {
    cookieWindow.style.pointerEvents = "none";
    cookieWindow.style.opacity = 0;
    setTimeout(() => {
      cookieWindow.remove()
    }, 250);
    sessionStorage.setItem('cookie', true);
  }
}

showCookie();